/* Buttons and Link Buttons */

/* button,
input[type="submit"],
input[type="button"], */
.btn {
  display: inline-flex;
  align-items: center;
  font-size: 1rem;
  padding: 8px 16px;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  background: var(--blue-50);
  color: var(--white);
  border: 2px solid var(--blue-50);
  border-radius: var(--radius-sm);
  box-sizing: border-box;
  cursor: pointer;
  transition: border 0.2s, background 0.2s, color 0.2s ease-out;

  /* &:before {
    content: "";
    display: inline-block;
    width: 0;
    height: 0;
    border-radius: 50%;
    border: 0 none;
    margin-right: 0;
    transition: all 0.5s;
  } */

  &:hover {
    background-color: var(--blue-40);
    color: var(--white);
    border: 2px solid var(--blue-40);
  }
  &:active {
    background-color: var(--blue-60);
    border: 2px solid var(--blue-60);
  }
  &:focus {
    box-shadow: 0 0 0px 2px rgba(60, 166, 208, 1);
  }
  &.btn-secondary {
    box-sizing: border-box;
    background: transparent;
    color: var(--link);
    border: 2px solid var(--gray-30);
    &:hover {
      background-color: transparent;
      color: var(--link-hover);
    }
  }

  &.btn-secondary {
    box-sizing: border-box;
    background: transparent;
    color: var(--link);
    border: 2px solid var(--gray-30);
    &:hover {
      background-color: transparent;
      color: var(--link-hover);
    }
  }
}

button:disabled,
input[type="submit"]:disabled,
input[type="button"]:disabled,
.button-disabled {
  cursor: default;
  background: var(--gray-5);
  color: var(--gray-50);
  border: 2px solid var(--gray-5);

  /* Set the [X] cursor while hovering a disabled link */
  cursor: not-allowed;
}

//
// Block button
//

.btn-block {
  display: block;
  width: 100%;
}

